/* Backend Status Styles */
.backend-status {
  margin-top: 10px;
  padding: 8px 12px;
  border-radius: 6px;
  font-size: 12px;
  font-weight: 500;
}

.status-checking {
  background: #fef3c7;
  color: #d97706;
  display: flex;
  align-items: center;
  gap: 6px;
}

.status-unavailable {
  background: #fee2e2;
  color: #dc2626;
  display: flex;
  align-items: center;
  gap: 6px;
}

.status-available {
  background: #d1fae5;
  color: #065f46;
  display: flex;
  align-items: center;
  gap: 6px;
}

/* Demo Credentials */
.demo-credentials {
  margin-top: 20px;
  padding: 15px;
  background: #f8fafc;
  border-radius: 8px;
  border: 1px dashed #cbd5e1;
  text-align: center;
}

.demo-credentials h4 {
  color: #475569;
  margin-bottom: 8px;
  font-size: 14px;
}

.demo-credentials p {
  color: #64748b;
  font-size: 12px;
  margin: 4px 0;
}

/* Loading Overlay */
.loading-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(255, 255, 255, 0.9);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.loading-spinner {
  width: 40px;
  height: 40px;
  border: 4px solid #e5e7eb;
  border-top-color: #1a237e;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}